const renderWarningToast = (params) => {
  const toastTranslations = config.translations.toast_labels.warning_toast

  const warningToast = $(`
    <div class='toast-wrap'>
      <div class='warning-toast'>
          <div class='toast-top'>
              <div class='img-wrap'>
                    <img src=${config.urls.staticImg + '/ic_sms.svg'} />
              </div>
              <div class='text-wrap'>
                  <p>${toastTranslations.sending_warning_reasons.no_sms.replace('{sms_warning_trashold}', config.sms_warning_trashold)}</p>
              </div>
          </div>
      </div>
    </div>
  `)

  return warningToast
}

export default renderWarningToast
