import './toasts.styl'
import renderSuccessToast from './successToast.jsx'
import renderFailureToast from './failureToast.jsx'
import renderWarningToast from './warningToast.jsx'

const renderToasts = (params = {}, action = 'add') => {
  let hideTimeout
  let failureToast
  let warningToast
  const successToast = renderSuccessToast(params.is_notification_sent && params.is_notification_sent.toString(), action)

  const toastHide = toast => {
    toast.children().addClass('hide')
    $('.floating-button').animate({
      bottom: 30 + 'px'
    }, 1000)
    $('.preloader').animate({
      bottom: 30 + 'px'
    }, 1000)
    setTimeout(() => {
      $('.toast-wrap').remove()
      if (params.is_sms_failed && params.is_sms_failed.toString() === 'true') {
        failureToast = renderFailureToast(params)
        params = {}
        toastShow(failureToast)
      }
      if (params.is_warning && params.is_warning.toString() === 'true') {
        warningToast = renderWarningToast(params)
        params = {}
        toastShow(warningToast)
      }
    }, 1000)
  }

  const toastShow = (toast = successToast) => {
    $('body').append(toast)

    if ($('.floating-button').length) {
      $('.floating-button').animate({
        bottom: 30 + toast.children()[0].offsetHeight + 'px'
      }, 750)
    }
    if ($('.preloader').length) {
      $('.preloader').animate({
        bottom: 30 + toast.children()[0].offsetHeight + 'px'
      }, 800)
    }

    const timeout = toast === successToast ? 3000 : 5000

    hideTimeout = setTimeout(() => { toastHide(toast) }, timeout)

    toast.children().on('click', () => {
      toastHide(toast)
      clearTimeout(hideTimeout)
    })
  }

  toastShow()

  const view = $('#calendar').fullCalendar('getView')
  history.pushState(null, null, `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}?worker_id=${config.activeWorkerId}&calendar_view_type=${new URLSearchParams(location.search).get('calendar_view_type')}`)
}

export default renderToasts
