import { newagendacalendar } from '../agenda/agenda'
import { checkShowEvent, closeDeletePopup } from 'helpers'
import { deleteEvent, patchEventDrop } from 'project-services'
import renderToasts from '../toasts/index.jsx'
import qs from 'qs'
import './deleteModal.styl'

let func

export const renderDeleteModal = (event, id, drop, revert) => {
  let view = $('#calendar').fullCalendar('getView')
  let obj = qs.parse(window.location.search.slice(1))
  const cancel = () => {
    $('.deleteModal').remove()
    document.removeEventListener('keydown', closeDeletePopup)
    if (drop) {
      revert()

      checkShowEvent()
    }
  }
  const confirm = () => {
    if (!drop) {
      clearTimeout(func)
      func = setTimeout(() => {
        deleteEvent(id).then(r => {
          if (r.status === 200) {
            $('#calendar').fullCalendar('removeEvents', [id])
            if (view.name === 'agendaDay') {
              let element = $('.fc-agendaDay-view ')
              newagendacalendar(view, element)
            }
            $('.eventPopupCont').remove()
            document.removeEventListener('keydown', closeDeletePopup)
            history.replaceState(null, null, window.location.search ? `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}` + window.location.search : `${config.urls.startUrl}/${view.intervalStart.format('YYYY-MM-DD')}?worker_id=${config.activeWorkerId}&calendar_view_type=${obj.calendar_view_type}`)
            checkShowEvent()
            r.json().then(obj => {
              const newToast = setInterval(() => {
                if (!$('.toast-wrap').length) {
                  renderToasts(obj, 'delete')
                  clearInterval(newToast)
                }
              }, 1000)
            })
          }
        })
      }, 50)
    } else {
      const appointmentId = event.id
      const start = moment(event.start).format('YYYY-MM-DD HH:mm:ss')
      patchEventDrop(appointmentId, start).then(r => {
        checkShowEvent(moment(event.start))
        if (r.status === 200) {
          $('.deleteModal').remove()
          document.removeEventListener('keydown', closeDeletePopup)
          r.json().then(obj => {
            const newToast = setInterval(() => {
              if (!$('.toast-wrap').length) {
                renderToasts(obj, 'edit')
                clearInterval(newToast)
              }
            }, 1000)
          })
        }
      })
    }
  }
  const clientName = event.name && event.name !== ''
    ? event.name
    : config.translations.occasional

  const sameDay = moment(window.loadedEvents.find(x => x.id === event.id).start).isSame(event.start, 'day')

  const dragQuestion = sameDay
    ? config.translations.drag_question_event_today.replace('{time}', moment(event.start).format('hh:mm'))
    : config.translations.drag_question_event_another_day
      .replace('{day}', moment(event.start).format('DD'))
      .replace('{month}', moment(event.start).format('MM'))
      .replace('{time}', moment(event.start).format('hh:mm'))
  const question = drop
    ? dragQuestion
    : config.translations.del_question_appointment.replace('{client_name}', clientName)

  const button = drop
    ? 'drag_'
    : 'deletion_popup_'

  const confirmModal = $(
    `<div id='modal-background' class='deleteModal${drop ? ' dropEvent' : ''}'>
      <div class='confirm-block'>
          <figure class='modal-body'>
            <img class='icon' src=${config.urls.staticImg + (drop ? '/modal-drag.svg#calendar' : '/trash-del.svg')} />
            ${!drop && event.off_time
              ? `<figcaption class='text'>${config.translations.del_question_off_time.replace('{off_time_type}', config.translations.off_time_labels[event.off_time])}</figcaption>`
              : `<figcaption class='text'>${question}</figcaption>`
            }
            
          </figure>
        <div class='modal-footer'>
          <button class='no-btn'>${config.translations[button + 'cancel'].toUpperCase()}<img class='cancel-img' src=${config.urls.staticImg + '/plus-blue.svg'} /></button>
          <button class='yes-btn'>${config.translations[button + 'confirm'].toUpperCase()}<img src=${config.urls.staticImg + '/confirm.svg'} /></button>
        </div>
      </div> 
    </div>`
  )
  if (!$('.deleteModal').length) {
    $('body').append(confirmModal)
  }
  $('.dropEvent').length === 0 && document.addEventListener('keydown', closeDeletePopup)
  $('.modal-body').on('click', e => e.stopPropagation())
  $('.no-btn').on('click', cancel)
  $('.deleteModal').on('click', () => {
    !drop && $('.deleteModal').remove()
    !drop && document.removeEventListener('keydown', closeDeletePopup)
  })
  $('.yes-btn').one('click', confirm)
}
