const renderSuccessToast = (isNotificationSent, action = 'add') => {
  const toastTranslations = config.translations.toast_labels.success_toast
  const firstString = action === 'add'
    ? toastTranslations.new_appointment_booked
    : action === 'delete'
      ? toastTranslations.appointment_deleted
      : toastTranslations.appointment_edited
  const secondString = isNotificationSent === 'true'
    ? action === 'add'
      ? toastTranslations.new_appointment_notifications
      : action === 'delete'
        ? toastTranslations.deleted_appointment_notifications
        : toastTranslations.edited_appointment_notifications
    : ''
  const successToast = $(
    `<div class='toast-wrap'>
        <div class='success-toast'>
            <div class='img-wrap'>
                <img src=${config.urls.staticImg + '/ic_check_mark.svg'} />
            </div>
            <div class='text-wrap'>
                <p>${firstString}</p>
                <p>${secondString}</p>
            </div>
        </div>
      </div>`
  )

  return successToast
}

export default renderSuccessToast
