export const timePanel = () => {
  let view = $('#calendar').fullCalendar('getView')

  const slatsHeights = []
  if(view.name === 'daily' || view.name === 'agendaFourDay') {
    const marginTopForSlats = config.workers.length > 1 ? $('.swiper-cont').innerHeight() + $('#header').innerHeight() : $('#header').innerHeight()
    if ($('#calendar').innerHeight() < document.documentElement.clientHeight - marginTopForSlats) {
      const minSwipeFixer = view.name === 'daily' ? 1 : 12
      const newHeight = Math.floor(document.documentElement.clientHeight - marginTopForSlats - minSwipeFixer)
      $('.fc-slats')[0].children[0].style.minHeight = newHeight + 'px'
      $('#calendar').fullCalendar('option', 'height', newHeight)
      // $('#calendar').fullCalendar('option', 'contentHeight', newHeight)

      for (let i = 0; i < $('.fc-slats')[0].children[0].children[0].children.length; i++) {
        slatsHeights.push($('.fc-slats')[0].children[0].children[0].children[i].clientHeight)
      }
    }
    if (view.name === 'daily' && $('.timeBox').length === 0) {
      let container = $('.fc-daily-view .fc-body')
      let timeBox = $('<div>', { class: 'timeBox' }).appendTo(container)
      let boxWidth = $('.fc-bg .fc-axis.fc-widget-content').innerWidth()
      let boxHeight = $('.fc-daily-view .fc-time-grid .fc-slats').innerHeight()
      let timeWeekHeight = $('.fc-head .fc-day-header').innerHeight()
      $(timeBox).css({
        'width': boxWidth + 1,
        'height': boxHeight + 0
      })
      let arrSpan = $('.fc-axis.fc-time span')
      let div = arrSpan.map((index, item) => {
        return `<div class='time-week' ${slatsHeights.length ? 'style="height:' + slatsHeights[index] + 'px"' : ''}><span class='time-text'>${$(item).text()}</span></div>`
      })
      let timeHtml = [...div].join('')
      $(timeHtml).appendTo($('.timeBox'))
      setTimePanelTime()
    }
    if (view.name === 'agendaFourDay' && $('.timeBox').length === 0) {
      let timeBox = $('<div>', { class: 'timeBox' }).appendTo($('.fc-agendaFourDay-view'))
      $('<div>', { class: 'header-week' }).appendTo($('.timeBox'))
      let boxWidth = $('.fc-bg .fc-axis.fc-widget-content').innerWidth()
      let boxHeight = $('.fc-agendaFourDay-view').height()
      let timeWeekHeight = $('.fc-head .fc-day-header').innerHeight()
      $(timeBox).css({
        'width': boxWidth + 1,
        'height': boxHeight
      })
      $('.header-week').css({ 'height': timeWeekHeight + 2 })
      let arrSpan = $('.fc-axis.fc-time span')
      let div = arrSpan.map((index, item) => {
        return `<div class='time-week' ${slatsHeights.length ? 'style="height:' + slatsHeights[index] + 'px"' : ''}><span class='time-text'>${$(item).text()}</span></div>`
      })
      let timeHtml = [...div].join('')
      $(timeHtml).appendTo($('.timeBox'))
      setTimePanelTime()
    }
    $('.timeBox').css(config.calendar.isRTL ? {
      'right': 0,
      'borderLeft': '1px solid #ededed'
    } : { 'left': 0 })
  }
}

const setTimePanelTime = () => {
  let view = $('#calendar').fullCalendar('getView')
  if (view.name === 'daily') {
    let timeBlock = $('.time-text').slice(1)
    timeBlock.map((index, element) => {
      if ($(element).text().slice(3) === '00') {
        let a = $(element).html().slice(0, 2)
        $($(element).parent()[0]).addClass('true-time-daily')
        $(element).addClass('margin-time')
        return $(element).html(a)
      } else if ($(element).text().length > 2) {
        $(element).text('')
      }
    })
    let firstSlot = $('.time-text')[0]
    $(firstSlot).addClass('margin-time')
    $(firstSlot).html($(firstSlot).text().slice(0, 2))
  }
  if (view.name === 'agendaFourDay') {
    let timeBlock = $('.time-text').slice(1)
    timeBlock.map((index, element) => {
      if ($(element).text().slice(3) === '00') {
        let a = $(element).html().slice(0, 2)
        $($(element).parent().prev()[0]).addClass('true-time')
        $(element).addClass('margin-time')
        return $(element).html(a)
      } else if ($(element).text().length > 2) {
        $(element).text('')
      }
    })
    let firstSlot = $('.time-text')[0]
    $(firstSlot).addClass('margin-time')
    $(firstSlot).html($(firstSlot).text().slice(0, 2))
  }
}
