import { patchEventResize, patchEventDrop } from 'project-services'
import { showEventPopUp } from '../app/components/baseEvents'
import { renderDeleteModal } from '../app/components/deleteModal/deleteModal'

export const checkDragging = e => {
  const classList = e?.gesture?.target
  const parentNode = classList ? $(classList).closest('div.info-time', $('div.fc-content.ev-custom')) : false
  const parentNodeName = parentNode && parentNode.length > 0 ? parentNode[0].className : false
  const rezult = parentNodeName === 'info-time'
  return rezult
}

export const checkShowEvent = defaultValue => {
  let view = $('#calendar').fullCalendar('getView')
  let calendarEvents = $('#calendar').fullCalendar('clientEvents')
  if (view.name === 'daily') {
    let start = moment(view.dateProfile.date).subtract(1, 'days').format('YYYY-MM-DD')
    let end = moment(start).add(2, 'days').format('YYYY-MM-DD')
    let currentEvents = calendarEvents.filter(i => {
      let eventStart = moment(i.start).format('YYYY-MM-DD')
      if (moment(eventStart).isBetween(start, end)) {
        return i
      }
    })
    showEventPopUp(currentEvents)
  } else if (view.name === 'agendaFourDay') {
    const dateValue = view?.dateProfile?.date ? view.dateProfile.date : defaultValue
    const start = moment(dateValue).subtract(5, 'days').format('YYYY-MM-DD')
    const end = moment(start).add(9, 'days').format('YYYY-MM-DD')
    const currentEvents = calendarEvents.filter(i => {
      const eventStart = moment(i.start).format('YYYY-MM-DD')
      if (moment(eventStart).isBetween(start, end)) {
        return i
      }
    })
    showEventPopUp(currentEvents)
  }
}

export const eventResize = event => {
  const appointmentId = event.id
  const end = moment(event.end).format('YYYY-MM-DD HH:mm:ss')
  patchEventResize(appointmentId, end).then(checkShowEvent())
}

export const eventDrop = (event, delta, revert) => {
  renderDeleteModal(event, event.id, true, revert)
}
