import { changeView, checkShowEvent, showPopupFromUrl, checkDragging } from 'helpers'
import { fetchEvenes, holidayData, scrollToEvent } from '../baseEvents'
import qs from 'qs'
import './daily.styl'

const dailyHoliday = () => {
  $('#calendar').fullCalendar('option', 'height', $('#calendar').innerHeight())
  if (localStorage.getItem('holidays_data') && !$('.holiday').length) {
    try {
      let holidaysData = JSON.parse(localStorage.getItem('holidays_data'))
      let data = holidaysData.data
      let calendarDate = $('#calendar').fullCalendar('getDate').format('YYYY-MM-DD')
      let holiday
      for (let key in data) {
        holiday = data[key].find(item => item.date === calendarDate)
        if (holiday && calendarDate === holiday.date) {
          $('.holiday').length && $('.holiday').length > 0 && $('.holiday').remove()
          $('.fc-daily-view .fc-head').append(`<td class=holiday>${holiday.name}</td>`)
          const heightWithHoliday = $('#calendar').innerHeight() + $('.holiday')[0].clientHeight
          $('#calendar').fullCalendar('option', 'height', heightWithHoliday)
        }
      }
    } catch (error) {
      localStorage.removeItem('holidays_data')
      holidayData()
    }
  }
}
export const newDaily = () => {
  $('#calendar').fullCalendar('removeEvents', 'limiter')
  const view = $('#calendar').fullCalendar('getView')
  const obj = qs.parse(window.location.search.slice(1))
  if (view.name === 'daily') {
    $('.fc-daily-view .fc-head').empty()
    const calendar = $('#calendar')
    calendar.fullCalendar()
    calendar.hammer().off('swipeleft')
    calendar.hammer().off('swiperight')
    holidayData(() => dailyHoliday())
    dailyHoliday()
    fetchEvenes(false, () => {
      checkShowEvent()
      showPopupFromUrl()
      scrollToEvent(obj)
    })
    $('.fc-time-grid-container').hammer().off('swiperight')
    $('.fc-time-grid-container').hammer().on('swiperight', e => {
      if (checkDragging(e)) {
        return
      } else {
        setTimeout(() => {
          $('#calendar').fullCalendar('removeEvents', 'limiter')
          config.calendar.isRTL ? calendar.fullCalendar('next') : calendar.fullCalendar('prev')
          $('.fc-daily-view .fc-head').empty()
          holidayData(() => dailyHoliday())
          dailyHoliday()
          fetchEvenes(false, () => {
            checkShowEvent()
            showPopupFromUrl()
          })
          changeView()
        }, 0)
      }
    })
    $('.fc-time-grid-container').hammer().off('swipeleft')
    $('.fc-time-grid-container').hammer().on('swipeleft', e => {
      if (checkDragging(e)) {
        return
      } else {
        setTimeout(() => {
          $('#calendar').fullCalendar('removeEvents', 'limiter')
          config.calendar.isRTL ? calendar.fullCalendar('prev') : calendar.fullCalendar('next')
          $('.fc-daily-view .fc-head').empty()
          holidayData(() => dailyHoliday())
          dailyHoliday()
          fetchEvenes(false, () => {
            checkShowEvent()
            showPopupFromUrl()
          })
          changeView()
        }, 0)
      }
    })
  }
}
