const renderFailureToast = (params) => {
  const toastTranslations = config.translations.toast_labels.failure_toast
  const errorReason = params.error_reason

  const failureToast = $(`
    <div class='toast-wrap'>
      <div class='red-toast'>
          <div class='toast-top'>
              ${errorReason === 'no_sms'
                ? `<div class='img-wrap'>
                    <img src=${config.urls.staticImg + '/ic_sms.svg'} />
                  </div>`
                : ''
              }
              <div class='text-wrap'>
                  <p>${toastTranslations.sending_failure_reasons[errorReason]}</p>
              </div>
          </div>
          ${errorReason === 'no_sms' 
            ? `<div class='button-wrap'>
                <button class='buy-sms'>
                  <p>Buy Sms</p>
                  <img class=${config.calendar.isRTL ? 'rtl' : 'ltr'} src=${config.urls.staticImg + '/ic_arrow_right.svg'} />
                </button>
              </div>`
            : ''}
      </div>
    </div>
  `)

  if (errorReason === 'no_sms') {
    failureToast.on('click', () => {window.location.href = config.urls.sms_settings_link})
  }

  return failureToast
}

export default renderFailureToast
